body {
  display: flex;
  flex-flow: column;
  align-items: center;
  /* overflow: clip; */

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 4px solid black; */
}

header {
  background-image: url(../imgs/main2_edited.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  width: 100%;
  height: 70vh;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 4px solid rgb(177, 20, 143); */
}

.menu {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  position: absolute;
  top: -5px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(20, 177, 35); */
}

.menu.sticky {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  z-index: 99;

  transition: all;
  transition-duration: 500ms;
  transition-timing-function: ease;
  position: fixed;
}

.menu.hidden {
  opacity: 0;
  transform: translateY(8rem);
}

.menubars-svg {
  display: none;
}

#menubars-svg {
  display: none;
}

#show-menu-button {
  display: none;
}

.shoppingcart-svg {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  fill: rgb(0, 179, 255);
  width: 25px;
  height: 25px;
  /* position: absolute; */
  left: 600px;
}

.shoppingcart-svg:hover {
  fill: rgb(121, 221, 255);
}

.cart-number {
  font-size: 0.9rem;
  position: absolute;
  right: 6.5%;
  top: 13%;
  background-color: rgb(255, 132, 0);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  text-align: center;
}

.menu__ul {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0.8);
  width: 100%;
  height: 50px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(228, 152, 37); */
}

.li {
  width: 100%;
  height: 50px;
  border-right: 1px solid black;
  list-style: none;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(46, 37, 228); */
}

.li:hover {
  background-color: rgba(255, 255, 255, 0.925);
  cursor: pointer;
}

.li.sticky:hover {
  background-color: rgba(212, 199, 171, 0.925);
}

a {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 50px;
  color: black;
  font-size: 1.1rem;
  text-decoration: none;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(40, 228, 37); */
}

.categories-list {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  text-align: center;
  list-style: none;
  border: 1px solid;
  position: relative;
  top: -20rem;
  background-color: rgba(250, 235, 215, 0.5);
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: linear;
  opacity: 0;
}

.categories-list--active {
  opacity: 1;
  z-index: 99;
  transform: translateY(20rem);
}

.category-item {
  position: relative;
}

.category-item:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
}

main {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  overflow: hidden;

  max-height: 100rem;
  width: 100%;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(37, 136, 228); */
}

h1 {
  font-weight: 100;
  background-color: rgba(255, 166, 0, 0.4);
  font-family: MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial,
    sans-serif;
  text-align: center;

  align-self: center;
  width: 100%;
  margin-top: 2rem;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(40, 228, 37); */
}

.first-row-container,
.second-row-container {
  display: flex;
  flex-flow: row;
  box-sizing: border-box;
  margin-bottom: 8rem;

  width: 100%;
  max-height: 100%;

  /* ~~~~ FOR DEBUGGING~~~~ */
  /* border: 2px solid rgb(37, 50, 228); */
}

.first-row-container {
  margin-top: -7rem;
}

