  /* border: 2px solid rgb(37, 155, 228); */
}

.workshop-description--img {
  width: 50%;
  height: 100%;

  box-shadow: 1px 3px 30px 2px rgba(133, 143, 143, 0.646);

  /* ~~~~ FOR DEBUGGING~~~~ */
  /* border: 2px solid rgb(129, 37, 228); */
}

.img-background {
  position: relative;
  top: 17rem;
  height: 12rem;
  width: 100%;
  background-color: aquamarine;
  z-index: -10;
}

.img-background-vertical {
  position: relative;
  top: -10rem;
  right: 35rem;
  width: 12rem;
  height: 100rem;
  background-color: rgba(163, 127, 255, 0.3);
  box-shadow: 3px;
  align-self: self-end;
  transform: rotate(30deg);
  z-index: -10;
}

.img-background-middle {
  position: relative;
  top: 18rem;
  height: 10rem;
  width: 100%;
  background-color: aquamarine;
  z-index: -1;
}

.img-background-last {
  position: relative;
  top: 18rem;
  height: 10rem;
  width: 100%;
  background-color: aquamarine;
  z-index: -10;
}

.workshop-costs {
  background-color: rgba(245, 245, 220, 0.9);
  box-shadow: 12px 2px 30px 2px rgb(168, 163, 154);
  width: 60vw;
  font-size: 1.2rem;
  padding: 1rem;

  text-align: center;
  align-self: center;

  /* ~~~~ FOR DEBUGGING~~~~ */
  /* border: 2px solid rgb(37, 155, 228); */
}

.go-to-top {
  position: absolute;
  top: 120rem;
  border: 1px solid;
  width: 10%;
  background-color: rgba(255, 255, 255, 0.712);
  padding: 5px;
  /* ~~~~ FOR DEBUGGING~~~~ */
  /* border: 2px solid rgb(37, 155, 228); */
}

.go-to-top:hover {
  border: rgb(113, 101, 33), solid;
  color: rgb(113, 101, 33);
  cursor: pointer;
  background-color: rgba(196, 106, 181, 0.377);
